export const autoContrast = (ctx: CanvasRenderingContext2D) => {

    const imageData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);
    const data = imageData.data;

    const getBrightness = (r, g, b) => {
        return 0.299 * r + 0.587 * g + 0.114 * b;
    }

    let brightnessSum = 0;
    let brightnessCount = 0;
    let contrastSum = 0;
    let contrastCount = 0;

    for (let i = 0; i < data.length; i += 4) {
        const r = data[i];
        const g = data[i + 1];
        const b = data[i + 2];
        const brightness = getBrightness(r, g, b);
        brightnessSum += brightness;
        brightnessCount++;

        const contrast = Math.abs(brightness - 128);
        contrastSum += contrast;
        contrastCount++;
    }

    const brightnessAvg = brightnessSum / brightnessCount;
    const contrastAvg = contrastSum / contrastCount;

    const brightnessFactor = 128 - brightnessAvg;
    const contrastFactor = 128 / contrastAvg;
    for (let i = 0; i < data.length; i += 4) {
        let r = data[i];
        let g = data[i + 1];
        let b = data[i + 2];
        r += brightnessFactor;
        g += brightnessFactor;
        b += brightnessFactor;
        r = Math.min(Math.max(Math.round(r * contrastFactor), 0), 255);
        g = Math.min(Math.max(Math.round(g * contrastFactor), 0), 255);
        b = Math.min(Math.max(Math.round(b * contrastFactor), 0), 255);
        data[i] = r;
        data[i + 1] = g;
        data[i + 2] = b;
    }

    ctx.putImageData(imageData, 0, 0)
    // var outputImg = document.createElement('img');
    // outputImg.src = ctx.canvas.toDataURL();
    // document.body.appendChild(outputImg);

    // img.src = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTFrr_PyLstPq8n38kb5Dap2ejPb7B9IreQYw&usqp=CAU';
    // img.src = 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSMBj1TbiAQDjCAtXgTRuZKb82qOem2vUosVw&usqp=CAU';

    // img.src = 'https://media.istockphoto.com/id/664494334/photo/closeup-young-smiling-woman-face-isolated-on-white.jpg?s=612x612&w=is&k=20&c=Nni6E0gDKxCDnT66c66t1vVfawzCQEc0B8taMlItvZM=';


    // img.src = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram_01.jpg'
}