import { RendererCanvas, getImgFromURL, ditheringFloydSteinberg } from '../../../lib';
// import { } from '../../../'
import { setData } from '../../DB/FireBase';
import { autoContrast } from './autoContrast';

export enum ColorCorrection {
    AutoContrast = 'autoContrast',
    None = 'None'
}

export class Solution {
    public divHost: HTMLElement;
    public renderer: Renderer;
    constructor(id: string) {
        this.divHost = document.getElementById(id)

        // Center align
        this.divHost.style.display = 'flex';
        this.divHost.style.alignItems = 'center';
        const div = document.createElement('div');
        div.style.marginLeft = 'auto';
        div.style.marginRight = 'auto';
        div.style.backgroundColor = '#555';
        div.style.margin = '50px';
        div.style.background = 'rgba(0,0,0,0)';
        this.divHost.appendChild(div);

        this.renderer = new Renderer(div);
    }
    public destroy() {
        this.renderer.destroy();
        while (this.divHost.lastElementChild) {
            this.divHost.removeChild(this.divHost.lastElementChild);
        }
    }
}

export class Renderer extends RendererCanvas {
    img: HTMLImageElement | undefined = undefined
    rotation = 0.0;
    m: number[] = [0, 0];

    switch = true;
    isDithering = false;
    typeCorrection: ColorCorrection = ColorCorrection.AutoContrast;
    constructor(div: any) {
        super(div);

        this.ctx = this.canvas.getContext('2d', { willReadFrequently: true }) as CanvasRenderingContext2D;

        this.canvas.style.position = 'relative';
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';

        // getImgFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram.jpg').then( (img: HTMLImageElement) => {
        getImgFromURL('https://www.glenstone.org/wp-content/uploads/2018/07/AV_Landscape-Hero-Contour-2993-1276x800.jpg').then((img: HTMLImageElement) => {
            this.img = img;
            this.img.onload = (ev) => {
                this.update(img);
            }
        });
        const divInput = document.createElement('input');
        divInput.value = 'https://media.istockphoto.com/id/664494334/photo/closeup-young-smiling-woman-face-isolated-on-white.jpg?s=612x612&w=is&k=20&c=Nni6E0gDKxCDnT66c66t1vVfawzCQEc0B8taMlItvZM=';
        divInput.addEventListener('keydown', (e: KeyboardEvent) => {
            if (e.code === 'Enter') {
                getImgFromURL(divInput.value).then((img: HTMLImageElement) => {
                    this.img = img;
                    this.img.onload = (ev) => {
                        this.update(img);
                    }
                });
            }

        })
        this.divHost.appendChild(divInput);

        const divMain_01 = document.createElement('div');
        divMain_01.style.display = 'flex';
        divMain_01.style.marginTop = '15px';
        this.divHost.appendChild(divMain_01);


        const slType = getSlider('auto', 0, 1);

        slType.slider.defaultValue = '0';
        this.divHost.appendChild(slType.div);
        slType.slider.oninput = () => {
            const value = parseInt(slType.slider.value);

            if (value === 0) {
                this.typeCorrection = ColorCorrection.AutoContrast;
                slType.text.textContent = ColorCorrection.AutoContrast;
            } else {
                this.typeCorrection = ColorCorrection.None;
                slType.text.textContent = ColorCorrection.None;
            }
            this.render(this.ctx, this.typeCorrection);
        };

        const divButtonImgLoad = buttonImgLoad((img: HTMLImageElement) => {
            this.img = img;
            this.canvas.width = this.img.width;
            this.canvas.height = this.img.height;
            this.render(this.ctx, this.typeCorrection);
        });
        divMain_01.appendChild(divButtonImgLoad);

        const checkBox = getCheckBox();
        checkBox.onchange = () => {
            this.isDithering = !this.isDithering;
            this.render(this.ctx, this.typeCorrection);
        }
        divMain_01.appendChild(checkBox);


        this.start();
    }
    public update(img: HTMLImageElement) {
        this.img = img;
        this.canvas.width = this.img.width;
        this.canvas.height = this.img.height;
        this.render(this.ctx, this.typeCorrection);
    }
    public render(ctx: CanvasRenderingContext2D, typeDitheringType?: ColorCorrection): void {
        if (this.img === undefined) { return; }
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        if (this.img) {
            this.ctx.drawImage(this.img, 0, 0);
        }
        this.ctx.restore();

        switch (typeDitheringType) {
            case ColorCorrection.AutoContrast:
                autoContrast(ctx);

                if (this.isDithering) {
                    ditheringFloydSteinberg(ctx);
                }
                // ditheringFloydSteinberg(ctx);
                // DitheringFloyd.make(ctx);
                break;
            // case AutoContrast.BayerOrderedDithering:
                // dithering4_4(ctx);
                // break;
            default:
                if (this.isDithering) {
                    ditheringFloydSteinberg(ctx);
                }
                break;
        }

        // !! can stop render after this frame.
        this.isStatic = true;
    }
}
const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};
const buttonImgLoad = (callback: (img: HTMLImageElement) => void) => {
    const inputFile = document.createElement('input');
    inputFile.type = 'file';
    inputFile.onchange = (e) => {
        //@ts-ignore
        if (e.target.files) {
            //@ts-ignore
            let imageFile = e.target.files[0]; //here we get the image file
            var reader = new FileReader();
            reader.readAsDataURL(imageFile);
            reader.onloadend = (e) => {
                const img = new Image(); // Creates image object
                //@ts-ignore
                img.src = e.target.result; // Assigns converted image to image object
                img.onload = (ev) => {
                    callback(img);
                }
            }
        }
    };
    return inputFile;
};
const getCheckBox = () => {
    const inputFile = document.createElement('input');
    inputFile.type = 'checkbox';
    return inputFile;
};